import PropTypes from "prop-types"
import React from "react"
import { Nav, Navbar } from "react-bootstrap"

class NavData {
  constructor(linkName, link, icon) {
    this.linkName = linkName
    this.link = link
    this.icon = icon
  }
}

const Header = ({ pageTitle, navItems, icon }) => (
  <Navbar expand="md" bg="primary">
    <img src={icon} alt="Logo" className="logo-image" />
    <Navbar.Brand className="text-black font-weight-bold">
      {pageTitle}
    </Navbar.Brand>
    {renderNav(navItems)}
  </Navbar>
)

function renderNav(navItems) {
  if (!Array.isArray(navItems) || !navItems.length) {
    return ""
  }
  return (
    <>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse
        id="responsive-navbar-nav"
        className="justify-content-end"
      >
        <Nav justify>
          {navItems.map(navData => (
            <Nav.Link href={navData.link} key={navData.link}>
              {navData.linkName}
            </Nav.Link>
          ))}
        </Nav>
      </Navbar.Collapse>
    </>
  )
}

Header.propTypes = {
  pageTitle: PropTypes.string,
  navItems: PropTypes.array,
}

Header.defaultProps = {
  pageTitle: ``,
  navItems: [],
}

export { Header, NavData }

